import React, { useState } from "react";
import styles from "./ReactSheet.module.css";

function ReactSheetDialog(props) {
  const [open, setOpen] = useState(false);
  const { modal } = props;

  const openSheet = () => {
    setOpen(true);
  };

  const closeSheet = () => {
    setOpen(false);
  };

  const handleEscKey = (event) => {
    if (event.key === "Escape") {
      closeSheet();
    }
  };

  const trapFocus = (event) => {
    if (!open) {
      return;
    }

    const isTabPressed = event.key === "Tab" || event.keyCode === 9;

    if (!isTabPressed) {
      return;
    }

    const trapEl =
      'button, [href], input, select, textarea, [tabindex]:not([tabindex="-1"])';
    const trapContent = document.querySelectorAll(trapEl);
    const firstTrapEl = trapContent[0];
    const lastTrapEl = trapContent[trapContent.length - 1];

    if (event.shiftKey) {
      if (document.activeElement === firstTrapEl) {
        lastTrapEl.focus();
        event.preventDefault();
      }
    } else {
      if (document.activeElement === lastTrapEl) {
        firstTrapEl.focus();
        event.preventDefault();
      }
    }

    if (!document.getElementById("sheet-heading").contains(event.target)) {
      event.stopPropagation();
      document.getElementById("sheet-heading").focus();
    }
  };

  const modalClass = modal ? styles.modal : "";
  const sheetClass = open ? styles.open : "";

  return (
    <>
      <button onClick={openSheet}>Open Sheet</button>
      <p>Other page content, links ETC.</p>
      <p>
        For more information on how to create accessible sheets, check out the
        following resources:
      </p>
      <ul>
        <li>
          <a href="https://www.w3.org/TR/wai-aria-practices-1.1/#dialog_modal">
            WAI-ARIA Authoring Practices: Dialog Modal
          </a>
        </li>
      </ul>

      <div
        className={`${styles.sheet} ${modalClass} ${sheetClass}`}
        role="dialog"
        aria-labelledby="sheet-heading"
        aria-modal={modal}
        aria-hidden={!open}
        onKeyDown={trapFocus}
      >
        <div className={styles.sheetHeader}>
          <h2 id="sheet-heading" className={styles.sheetHeading} tabIndex="-1">
            Sheet Dialog
          </h2>
          
          <button className={styles.closeButton} onClick={closeSheet}>
            &times;
          </button>
        </div>
        <div className={styles.sheetContent}>
          <p>This is the sheet dialog content.</p>
        </div>
      </div>
    </>
  );
}

export default ReactSheetDialog;
